<template>
  <div id="app">
    <div class="resume">
      <!--  -->
      <div class="left">
        <Brief></Brief>
        <ContextList></ContextList>
      </div>
      <!--  -->
      <div class="right">
        <!--  -->
        <Description :title='title1'>
         <div class="education">
           <span class="school"></span>
           <span class="major"></span>
           <span class="date">2017.9 ~ 2019.6</span>
         </div>
        </Description>
        <!--  -->
        <Description :title='title2'>
          <div class="skill">
            <p class="left">
              1SpringSpringMVCMybatisMavenJQueryJava
              <br>
              2MysqlSqlServerSqlitesql
              <br>
              3IdeaEclipseVSHBuilder X
              <br>
              4Appapp
              <br>
              5VueElementBootStrap
            </p>
            <div class="right">
              <div class="canvas-container">
                <canvas ref="canvas" width="200" height="200"></canvas>
                <div ref="legend"></div>
              </div>
            </div>
          </div>
        </Description>
        <!--  -->
        <Description :title='title3'>
          <ExpItem :time='time1' :desc='desc1' :place='place1'></ExpItem>
          <ExpItem :time='time2' :desc='desc2' :place='place2'></ExpItem>
        </Description>
        <!--  -->
        <Description :title='title4'>
          <div class="education">
            <span class="school">IT</span>
            <span class="major"></span>
            <span class="date">2017.8 - 2017.9</span>
          </div>
          <p class="content">
            
          </p>
          <div class="education">
            <span class="school">IT</span>
            <span class="major"></span>
            <span class="date">2018.10</span>
          </div>
          <p class="content">
            
          </p>
        </Description>
        <!--  -->
        <Description :title='title5'>
          <p class="content">
            Java
            
            
          </p>
        </Description>
      </div>
    </div>
    <button class="button" @click='saveAsImage'></button>
  </div>
</template>

<script>
// conponents  api 
import Brief from "@/components/brief.vue";
import ContextList from "@/components/context-list.vue";
import Description from "@/components/description.vue";
import PieChart from "@/api/pieChart.js";
import ExpItem from "@/components/exp-item.vue";
import html2canvas from "@/api/html2canvas.js";
import FileSaver from "file-saver";

export default {
  name: "App",
  data: function() {
    return {
      title1: "",
      title2: "",
      title3: "",
      title4: "",
      title5: "",
      time1: "2018.9 ~ 2018.11",
      time2: "2018.12 ~ ",
      // EChart 
      piePercent: {
        English: 50,
        Vue: 40,
        Java: 80,
        JavaScript: 60
      },
      desc1:
        "Java",
      place1: "",
      desc2:
        "SSMVUE-CLI"+
        "",
        place2: "",
      colors: ["#fde23e", "#f16e23", "#57d9ff", "#937e88"]
    };
  },
  // EChart 
  mounted() {
    const myPieChart = new PieChart({
      canvas: this.$refs.canvas,
      data: this.piePercent,
      colors: this.colors,
      doughnutHoleSize: 0.5,
      legend: this.$refs.legend
    });
    myPieChart.draw();
  },
  //
  methods: {
    saveAsImage: function() {
      let resume = document.querySelector(".resume");
      html2canvas(resume).then(function(canvas) {
        canvas.toBlob(function(blob) {
          FileSaver.saveAs(blob, "Resume.png");
        });
      });
    }
  },
  //
  components: {
    Brief,
    ContextList,
    Description,
    ExpItem
  }
};
</script>

<style lang='less'>
@import url("../node_modules/reset-css/reset.css");
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 0;
  .resume {
    width: 1247px;
    height: 1754px;
    border: 1px solid #dad8d7;
    background-color: white;
    position: relative;
    overflow: auto;
    .left {
      width: 380px;
      height: 100%;
      box-sizing: border-box;
      float: left;
      background-color: #fdfefe;
    }
    .right {
      box-sizing: border-box;
      width: 867px;
      float: right;
      padding: 0px 40px;
      p {
        font-size: 20px;
        line-height: 32px;
        color: #555;
        text-align: justify;
      }
      .skill {
        display: flex;
        margin-top: 15px;
        justify-content: space-between;
        .left {
          width: 70%;
        }
        .right {
          width: 30%;
          .canvas-container {
            margin-top: 150px;
          }
        }
      }
      .education {
        font-size: 18px;
        font-weight: bold;
        display: flex;
        justify-content: space-between;
        margin-top: 8px;
        .school {
          width: 40%;
        }
        .major {
          width: 35%;
        }
        .date {
          width: 25%;
          text-align: right;
        }
      }
    }
    .bottom {
      position: absolute;
      bottom: 40px;
      left: 50%;
      transform: translateX(-50%);
      .text {
        font-size: 18px;
      }
    }
  }
  button.button {
    position: absolute;
    top: 40px;
    left: 50%;
    margin-left: 478px;
    background-color: white;
    border: 1px solid #dad8d7;
    cursor: pointer;
    padding: 5px 8px;
    border-radius: 4px;
    &:hover {
      background-color: #f8f8f8;
    }
  }
}
</style>



